body {
  font-family: 'Bebas Neue', sans-serif;
  position: absolute;
  width: 100%;
  height: 100vh;
  background-image: url("https://bipbap.ru/wp-content/uploads/2017/08/1375998858_1953444020.jpg");

  background-position: center bottom;

}


.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}


img {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  max-height: 100%;
  z-index: -1;

}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 10%;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 90%;
  max-width: 1600px;
  height: 80px;
  padding: 10px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.5);
}

@media screen and (max-width: 768px) {
  .navigation {
    flex-wrap: wrap;
    justify-content: center;
    height: auto;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.8);
  }

  .navigation__section {
    margin: 0 10px;
  }
}

.footer {
  position: absolute;
  bottom: 0%;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.5);
}

.footer__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  max-width: 600px;
}

.footer__section {
  margin: 0 10px;
}

.footer__icon {
  font-size: 32px;
  color: #333;
  transition: color 0.3s ease-in-out;
}

.footer__icon:hover {
  color: darkred;
}

 /*-----------------------стили для кнопки --------------------------*/
.btn{
  position: absolute;
  display: inline-block;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px 20px;
  background-color: rgba(255, 255, 255, 0.5);
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  font-family: 'Bebas Neue', sans-serif;
  text-decoration: none;
  letter-spacing: 4px;
  overflow: hidden;
}
.button__line{
  position: absolute;
  display: block;
}
.button__line--top{
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, red);
}
.btn:hover .button__line--top{
  left: 100%;
  transition: 1s;
  transition-delay: 0.25s;
}
.button__line--bottom{
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, red);
}
.btn:hover .button__line--bottom{
  right: 100%;
  transition: 1s;
  transition-delay: 0.25s;
}
.button__line--left{
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(0deg, transparent, red);
}
.btn:hover .button__line--left{
  bottom: 100%;
  transition: 1s;
  transition-delay: 0s;
}
.button__line--right{
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, red);
}
.btn:hover .button__line--right{
  top: 100%;
  transition: 1s;
  transition-delay: 0s;
}
.btn:hover{
  box-shadow: 0 0 10px #2196f3, 0 0 40px #2196f3, 0 0 80px #2196f3;
  background-color: #2196f3;
  transition-delay: 1s;
}








